.MainApp body {
    --color-background: #ffffff;
    --color-primary: #9facbb;
    --color-border: #4b98f0;
    --color-primary-dark: #5e666e;
    --color-error: #fd1717;
    --color-success: #0ce21d;
    --border-radius: 4px;

    margin: auto;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--color-background);
}

/* Hide sign up page */
.MainApp .form__hidden {
    display: none;
}

/* Set size and location of the container */
.MainApp .login__container {
    flex-direction: column;
    width: 400px;
    height: 450px;
    max-width: 400px;
    margin: auto;
    margin-top: 100px;
    margin-bottom: 100px;
    padding: 2rem;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
    border-radius: var(--border-radius);
    background: #f3f2f2e4;
}

/* Setting for Sign in text */
.MainApp .header {
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 50px;
    font-size: 37px;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

/* Eliminate the gap on top and bottom */
.MainApp .form > *:first-child {
    margin-top: 0;
}

.MainApp .form > *:last-child {
    margin-bottom: 0;
}

.MainApp .form__message {
    text-align: center;
    margin-bottom: 1rem;
}

.MainApp .form__message-success {
    color: var(--color-success);
}

.MainApp .form__message-error {
   
    color: var(--color-error);
}

/* Make space between boxes */
.MainApp .form__input-group {
    margin-bottom: 1rem;
}

/* Create grey box for user input */
.MainApp .form__input {
    display: block;
    width: 100%;
    padding: 0.75rem;
    box-sizing: border-box;
    border-radius: var(--border-radius);
    border: 1px solid #dddddd;
    outline: none;
    background: #eeeeee;
    transition: background 0.2s, border-color 0.2s;
}

/* Set color when column box is clicked */
.MainApp .form__input:focus {
    border-color: var(--color-border);
    background: #ffffff;
}

/* Set error message color */
.MainApp .form__input-error-message {
    margin-top: 0.5rem;
    font-size: 0.85rem;
    color: var(--color-error);
}

/* Change size and color of button */
.MainApp .form__button {
    width: 100%;
    padding: 1rem 2rem;
    font-weight: bold;
    font-size: 1.1rem;
    color: #261717;
    border: none;
    border-radius: var(--border-radius);
    outline: none;
    cursor: pointer;
}

/* Change color when mouse is hovered on button */
.MainApp .form__button:hover {
    background: #d4d7d8;
}

/* Shrink size of button when it's clicked */
.MainApp .form__button:active {
    transform: scale(0.98);
}

/* Position for 'Forgot pw and create acct link' */
.MainApp .form__text__forgot {
    margin-top: 5.5rem;
}

/* Position for 'back to login */
.MainApp .form__text__login__one {
    margin-top: 10.5rem;
}

/* Popsition for 'already have an accoun? log in' */
.MainApp .form__text__login__two {
    margin-top: 3.5rem;
}


.MainApp .form__text__createAcc {
    margin-top: 0.5rem;
}

/* Forgot password text setting */
.MainApp .forgot-password {
    color: #4797e7;
    font-size: 13px;
}

/* Setting for create account and log in button */
.MainApp .form__link {
    color: #2486e9;
    cursor: pointer;
    border: none;
    font-size: 15px;
    background-color: transparent;
}

.MainApp .form__link:hover {
    border-radius: 40px;
    background: #e2e4e7da;
}


.MainApp .background {
    background-image: url('../../assets/liger.png'); 
    height: 100vh;
    background-size: cover;
    background-position: center;
    
    background-repeat: no-repeat;
}

.MainApp .form__feedback {
    margin-top: 0.5rem;
}

.MainApp .feedback__message {
    font-size: 0.85rem;
    color: red;
    margin-bottom: 0.25rem;
}


.MainApp .feedback__message.success {
    color: var(--color-success);
    color: green;
}

.MainApp .feedback__message.error {
    color: var(--color-error);
    color: red;
}

.MainApp .password-container {
    position: relative;
    width: 100%;
}

.MainApp .password-input {
    padding-right: 50px;  
    padding-right: 45px;
    box-sizing: border-box;
}

.MainApp .toggle-password-visibility {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: #2486e9;
    cursor: pointer;
    font-size: 14px;
    padding: 0;
    line-height: 1;
}
  
  /* Mobile View (768px and below) */
  @media all and (max-width: 768px) {
    .MainApp .login__container {
      max-width: 80%; /* Take almost full width on mobile */
      padding: 1rem;
      align-items: center;
      height: 415px;
      
    }

    .MainApp .background {
        height: 80vh;
        background-position: center;
        justify-content: center;
        background-repeat: no-repeat; /* Prevent image from repeating */
        
    }
  
    .MainApp .header {
      font-size: 30px; /* Adjust header size */
    }
}

